"use client";

import React, { createContext, useContext, useState, useEffect, ReactNode } from "react";
import axios from "axios";
import { Book } from "@/app/lib/books";

interface BooksContextType {
  books: Book[];
  favorites: Book[];
  loading: boolean;
  error: string | null;
  fetchBooks: () => Promise<void>;
  addFavorite: (book: Book) => Promise<void>;
  removeFavorite: (id: string) => Promise<void>;
}

const BooksContext = createContext<BooksContextType | undefined>(undefined);

export const BooksProvider = ({ children }: { children: ReactNode }) => {
  const [books, setBooks] = useState<Book[]>([]);
  const [favorites, setFavorites] = useState<Book[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // üîπ Carregar livros do backend
  const fetchBooks = async () => {
    setLoading(true);
    setError(null);
    try {
      const res = await axios.get("/api/books");
      setBooks(res.data);
    } catch (err: any) {
      setError("Erro ao carregar livros.");
    } finally {
      setLoading(false);
    }
  };

  // üîπ Adicionar favorito
  const addFavorite = async (book: Book) => {
    try {
      await axios.post("/api/favorites", book);
      setFavorites((prev) => [...prev, book]);
    } catch (err) {
      setError("Erro ao adicionar favorito.");
    }
  };

  // üîπ Remover favorito
  const removeFavorite = async (id: string) => {
    try {
      await axios.delete(`/api/favorites/${id}`);
      setFavorites((prev) => prev.filter((fav) => fav.id !== id));
    } catch (err) {
      setError("Erro ao remover favorito.");
    }
  };

  // üîπ Carregar livros ao iniciar
  useEffect(() => {
    fetchBooks();
  }, []);

  return (
    <BooksContext.Provider
      value={{ books, favorites, loading, error, fetchBooks, addFavorite, removeFavorite }}
    >
      {children}
    </BooksContext.Provider>
  );
};

// üîπ Hook para consumir o contexto
export const useBooks = () => {
  const context = useContext(BooksContext);
  if (!context) {
    throw new Error("useBooks deve ser usado dentro de um BooksProvider");
  }
  return context;
};
